import { GoogleMap, useJsApiLoader } from '@react-google-maps/api';
import { InterfaceLocation } from '../../interface';
import Location from './Location';

export default function Maps(props: { 
    locations: InterfaceLocation[]
 }) {

    const { isLoaded } = useJsApiLoader({
        id: 'google-map-script',
        googleMapsApiKey: "AIzaSyCUwcwJ7u1_FUIMI9BYXSESuqsu2EBE-e4"
    })

    return isLoaded ? (
        <GoogleMap
            mapContainerStyle={{ width: "100%", height: "100%" }}
            center={{ lat: -6.2402773393971245, lng: 106.8160056927884 }}
            zoom={12}
        >
            {props.locations.map(location => (
                <Location key={location.name} location={location} />
            ))}
        </GoogleMap>
    ) : <></>
}